// src/components/LavaLampBackground.js
import React, { useRef } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { MeshWobbleMaterial } from "@react-three/drei";

function LavaLampBlob() {
  const meshRef = useRef();

  useFrame(({ clock }) => {
    const t = clock.getElapsedTime();

    meshRef.current.rotation.y = t / 4;
    meshRef.current.rotation.x = t / 6;

    const wobbleFactor = 0.2 + 0.15 * Math.sin(t * 2);
    meshRef.current.scale.set(
      1 + wobbleFactor,
      1 + wobbleFactor * 0.9,
      1 + wobbleFactor * 1.1
    );
  });

  return (
    <mesh ref={meshRef} position={[0, 0, 0]}>
      <icosahedronGeometry args={[2.5, 8]} />
      <MeshWobbleMaterial
        color="#ff69b4"
        speed={2}
        factor={0.5}
        envMapIntensity={1}
        clearcoat={1}
        clearcoatRoughness={0.1}
        metalness={0.5}
        roughness={0.25}
        transparent
        opacity={0.6}
      />
    </mesh>
  );
}

export default function LavaLampBackground() {
  return (
    <Canvas
      camera={{ position: [0, 0, 8], fov: 50 }}
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100vw",
        height: "100vh",
        zIndex: -1,
        pointerEvents: "none",
        background: "#1a0a2a",
      }}
    >
      <ambientLight intensity={0.5} />
      <pointLight position={[10, 10, 10]} intensity={1} />
      <LavaLampBlob />
    </Canvas>
  );
}